import { WatchlistDataTablePriceCell } from "../../WatchlistDataTableCells/WatchlistDataTablePriceCell"
import { genericColumn } from "../../genericColumn"
import { getCashFlowFiscalPeriod } from "../../utils/fiscalPeriodUtils"

function accountsReceivables(tableName: string) {
  const title = "Accounts Receivables (FQ)"
  return genericColumn({
    endpoint: "companyCashFlowStatement",
    columnKeys: ["accountsReceivables"],
    size: 130,
    title,
    enableSorting: true,
    enableHiding: true,
    tooltip: "Customer payments owed to the company.",
    cell: info => {
      return (
        <WatchlistDataTablePriceCell
          tooltip={`Explain the ${title}`}
          queryResult={info.row.original.financialData.companyCashFlowStatement}
          currency={
            info.row.original.financialData.companyCashFlowStatement?.data?.reportedCurrency
          }
          onClick={() => {
            const fiscalPeriod = getCashFlowFiscalPeriod(info.row.original)
            info.row.original.onChatWithCell?.({
              prompt: `What has ${info.row.original.entityInfo.name} reported in ${fiscalPeriod} about accounts receivables?`,
              value:
                info.row.original.financialData.companyCashFlowStatement?.data?.accountsReceivables,
              entityInfo: info.row.original.entityInfo,
              columnName: `${tableName}: ${title}`,
            })
          }}
          value={
            info.row.original.financialData.companyCashFlowStatement?.data?.accountsReceivables
          }
        />
      )
    },
  })
}

export { accountsReceivables }
